.coming-soon-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  height: 100vh;
  background-color: #f0f0f0;
}

h1 {
  font-size: 4rem;
  margin-bottom: 1rem;
}

.countdown-timer {
  display: flex;
  justify-content: center;
  align-items: center;
}

.countdown-item {
  margin: 0 1rem;
  text-align: center;
}

h2 {
  font-size: 4rem;
  margin-bottom: 0.5rem;
}

p {
  font-size: 1.5rem;
  margin-top: 1rem;
}
